<template>
  <div class="hello">
    <div class="main">
      <main class="content">
        <div class="container-fluid p-0">
          <div class="row">
            <div class="col-xl-6 col-xxl-6 d-flex mx-auto">
              <div class="w-100">
                <div class="row">
                  <div class="col-sm-12">
                    <div class="card">
                      <div class="card-body">
                        <h3 class="text-center m-0">
                          <img
                            src="../assets/padls_official_logo.png"
                            id="img"
                          />
                        </h3>

                        <div class="p-3">
                          <h1
                            class="text-muted font-1 m-b-5 text-center"
                            style="background-color"
                          >
                            VERIFY CERTIFICATE
                          </h1>
                          <br />
                          <h3>{{name}}</h3>
                          <h4>Date Collected: {{date1}}, 2021 04:00 PM</h4>
                          <h4>Date Released: {{date2}}, 2021 07:17 AM</h4>
                          <h4>PCR Result: NEGATIVE</h4>
                          <br />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      name: '',
      date1: '',
      date2: ''
    }
  },
  methods: {
    init () {
      this.name = this.$route.query.name ? this.$route.query.name : 'TIAM,MOFAN'
      this.date1 = this.$route.query.date1 ? this.$route.query.date1 : 'FEB 09'
      this.date2 = this.$route.query.date2 ? this.$route.query.date2 : 'FEB 10'
    }
  },
  activated () {
    this.init()
  },
  mounted () {
    this.init()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>
<style scoped>
#sidebar {
  display: none;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 25px;
  padding-right: 5px;
  padding-top: 5px;
}

.reeed {
  color: red;
}
.size {
  font-size: 20px;
}

.main-panel {
  /*position: relative;*/
  float: none;
  width: 80%;
  top: 20%;
  padding-top: 5px;
  margin-left: auto;
  margin-right: auto;
  background-color: #f4f3ef;
  transition: all 0.5s cubic-bezier(0.685, 0.0473, 0.346, 1);
}
h1 {
  font-size: 20px;
}
#img {
  width: 180px;
  height: 150px;
  margin: 5px;
  padding: 5px;
  border: 2px solid white;
}

#img_hdr {
  color: #525575;
  font-weight: 700;
}
</style>



